import { graphql, useStaticQuery } from "gatsby";
import React from "react";
import styled from "styled-components";
import {
  CenteredRow,
  BlockTitle,
  ColumnTitle,
  FullBlock,
  InvertedRow,
  Row,
  TextColumn,
} from "../components/content";
import { Main } from "../components/main";

const BlogGrid = styled(FullBlock)`
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  padding: 0;
  @media only screen and (max-width: 768px) {
    grid-template-columns: 1fr;
  }
`;

export default () => {
  const data = useStaticQuery(
    graphql`
      query {
        allContentfulBlogPost {
          edges {
            node {
              title
              url
              id
              summary {
                summary
              }
            }
          }
        }
      }
    `
  );
  return (
    <Main>
      <title>Kyle Watson - Blog</title>
      <CenteredRow>
        <FullBlock>
          <BlockTitle>Blog</BlockTitle>
          <BlogGrid>
            {data.allContentfulBlogPost.edges.map((edge, index) => {
              const StyledRow = index % 2 ? Row : InvertedRow;
              return (
                <div key={edge.node.id}>
                  <a href={edge.node.url}>
                    <ColumnTitle>{edge.node.title}</ColumnTitle>
                  </a>
                  <TextColumn>{edge.node.summary.summary}</TextColumn>
                </div>
              );
            })}
          </BlogGrid>
        </FullBlock>
      </CenteredRow>
    </Main>
  );
};
