import * as React from "react";
import styled from "styled-components";
import scrollTo from "gatsby-plugin-smoothscroll";
import Resume from "../images/Resume.pdf";

const StyledFooter = styled.div`
  display: flex;
  padding: 1rem 8rem;
  font-family: sans-serif;
  color: white;
  background-color: #001b2e;
  font-size: 1.2rem;
  @media only screen and (max-width: 768px) {
    padding: 1rem;
    display: block;
  }
`;
const Links = styled.div`
  padding: 0 4rem;
  @media only screen and (max-width: 768px) {
    padding: 0 1rem;
  }
`;
const ListTitle = styled.h3`
  font-size: 1.3rem;
  font-weight: bold;
`;
const List = styled.ul`
  list-style: none;
  padding: 0;
`;
const Link = styled.a`
  &:link {
    color: white;
  }
  &:visited {
    color: #f3b3a6;
  }
`;
const BackButtonContainer = styled.div`
  flex-grow: 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
`;
const BackButton = styled.button`
  color: #001b2e;
  font-weight: bold;
  font-size: 1.2rem;
  background-color: #f3b3a6;
  padding: 1rem;
  margin: 0;
  border: none;
  border-radius: 20px;
  box-shadow: #000000 0px 8px 8px;
  cursor: pointer;
  transition: ease 0.3s;
  :hover {
    transform: translateY(-5px);
  }
`;

const links = [
  { url: "/", label: "Resume" },
  { url: "/experience", label: "Experience" },
  { url: "/blog", label: "Blog" },
  { url: "/contact", label: "Contact" },
];
const contacts = [
  { url: "https://github.com/kylejwatson", label: "GitHub" },
  { url: "https://www.linkedin.com/in/kylejwatsonbsc/", label: "LinkedIn" },
  { url: "mailto:mail@watsonk.me", label: "Email" },
  { url: Resume, label: "Resume PDF" },
  { url: "tel:+31620747762", label: "Mobile Phone" },
];

export const Footer = () => {
  return (
    <StyledFooter>
      <Links>
        <ListTitle>Kyle Watson - Portfolio</ListTitle>
        <List>
          {links.map((link, index) => (
            <li key={index}>
              <Link href={link.url}>{link.label}</Link>
            </li>
          ))}
        </List>
      </Links>
      <Links>
        <ListTitle>Contact</ListTitle>
        <List>
          {contacts.map((link, index) => (
            <li key={index}>
              <Link href={link.url}>{link.label}</Link>
            </li>
          ))}
        </List>
      </Links>
      <BackButtonContainer>
        <div>
          <BackButton onClick={() => scrollTo("body")}>
            Back to the top
          </BackButton>
        </div>
      </BackButtonContainer>
    </StyledFooter>
  );
};
